/* Basic layout for the Organizers page */
.organizers-page {
    padding: 20px;
    background-color: #f8f9fa;
    font-family: Arial, sans-serif;
  }
  
  .page-title {
    text-align: center;
    font-size: 3rem;
    color: #333;
    margin-bottom: 40px;
  }
  
  .organizers-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    padding: 0 20px;
  }
  
  .organizer-card {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    opacity: 0;
    transform: translateY(100px);
    transition: opacity 0.5s, transform 0.5s;
  }
  
  .organizer-image {
    border-radius: 50%;
    width: 120px;
    height: 120px;
    object-fit: cover;
    margin-bottom: 15px;
  }
  
  .organizer-name {
    font-size: 1.5rem;
    font-weight: bold;
    color: #333;
  }
  
  .organizer-role {
    font-size: 1.2rem;
    color: #777;
    margin: 10px 0;
  }
  
  .organizer-description {
    font-size: 1rem;
    color: #555;
  }
  
  /* Trigger animation when the element comes into view */
  .organizer-card.animate {
    opacity: 1;
    transform: translateY(0);
  }
  
  