.textShow{
    animation: textShowAnimation 3s both  ;
    
}
@keyframes textShowAnimation{
    from{
        opacity: 0;
        transform: translateY(200px)scale(0.3);
    }
    to {
        opacity: 1;
        transform: translateY(0)scale(1);
    }
}
